import ComponentLayout from './ComponentLayout'
import { mixin } from './mixin'
import JSZip from 'jszip'

export default {
  name: 'DatasetZipViewer',
  components: { ComponentLayout },
  mixins: [mixin],
  data() {
    let data = {}
    return this.importData(data)
  },
  mounted() {},
  computed: {
    plugActionDisabled() {
      let disabled = 0
      disabled |= this.loading === true
      disabled |= this.inputData === null
      return disabled === 1
    }
  },
  methods: {
    validateActionEvent(event) {
      if (event === false && this.inputData !== null) {
        this.plugAction(event)
      }
    },
    plugActionEvent(event) {
      JSZip.loadAsync(this.inputData)
        .then(function(zip) {
          let files = Object.keys(zip.files)
          return zip.file(files[0]).async('arraybuffer')
        })
        .then(
          function(buffer) {
            this.output = buffer
            this.plugActionEnd(event)
          }.bind(this)
        )
    }
  }
}
</script>

